import React, {Component} from 'react';
import {Button, Card, List} from "antd";
import {Link, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import moment from "moment";



@withRouter
@connect(({ transaction }) => ({
  transaction,
  contentStatus: transaction.contentStatus,
}))
class OrderHistory extends Component {
  constructor(props) {
    super(props);

    this.componentRef = React.createRef();
    this.state = {
      modalReturn: false,
      from: moment().format("YYYY-MM-DD"),
      to: moment().format("YYYY-MM-DD"),
      custCode:""
    }
  }


  render() {

    const data = [
      // {
      //   title: 'Report Summary',
      //   url: 'pos/report/summary'
      // },

      {
        title: 'Laporan Detail by Anggota',
        url: 'pos/report/customer'
      },
      {
        title: 'Laporan Detail Item',
        url: 'pos/report/detail'
      },
      {
        title: 'Laporan Web',
        url: 'pos/report/customerWeb'
      },
    ];

    return (
      <div className='container mx-auto p-4 h-screen'>
        <div>
          <Link to ='/kasir'>
            <Button className='bg-green-500 text-white rounded leading-3'>ORDER BARU</Button>
          </Link>
        </div>


        <div className='mt-4'>
          <List
            grid={{ gutter: 16, column: 4 }}
            dataSource={data}
            renderItem={item => (
              <List.Item>
                <Link to={`/${item.url}`}>
                  <Card>
                    {item.title}
                  </Card>
                </Link>


              </List.Item>
            )}
          />


        </div>


      </div>
    );
  }
}

export default OrderHistory;

